export default {
  name: 'Home',
  data () {
    return {
      progress: 0,
      progressSteps: [],
      bgColor: config.COLORS[0]
    }
  },
  components: {
    FramePlayer,
    Section1,
    Section2,
    Section3,
    Section3A,
    Section4,
    Section4A,
    Section5,
    Section6,
    Section7,
    Section7A,
