    Section30,
    SectionInfo
  },
  methods: {
    ...mapMutations([
      'setCurPageIndex'
    ]),
    init () {
      // Frame pre page
      const { PAGE_FRAMES } = config

      const controller = new ScrollMagic.Controller()
      new ScrollMagic.Scene({
        triggerElement: '#scroll-wrap',
        offset: 0,
        duration: '100%',
        triggerHook: 0
      })
        .addTo(controller)
        // .setPin('#frame')
        .on('update', e => {
          const { endPos, scrollPos } = e
          // const step = endPos / FPP

          const page = Math.ceil(scrollPos / endPos)
          const pageIndex = page > 0 ? page - 1 : 0
          const step = endPos / PAGE_FRAMES[pageIndex]
          this.bgColor = config.COLORS[pageIndex] ? config.COLORS[pageIndex] : '#845151'

          this.progressSteps = PAGE_FRAMES.slice(0, pageIndex + 1)
          this.progressSteps[pageIndex] = Math.round((scrollPos - endPos * pageIndex) / step)
          this.progress = this.progressSteps.reduce((prev, next) => prev + next)

          this.setCurPageIndex(pageIndex)
          // console.log('index:' + pageIndex)
          // console.log(this.progressSteps)
          // console.log(this.progress)
        })
    }
  },
  mounted () {
    this.init()
  }
}
