import Vue from 'vue'
import Vuex from 'vuex'
import config from '@/config'
import isSupportWebp from '@/utils/is-support-webp'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loadedCount: 0,
    frames: [],
    curPageIndex: 0
  },
  getters: {
    loadingRate (state) {
      return Math.round((state.loadedCount / config.FRAME_COUNT) * 100)
    }
  },
  mutations: {
    setCurPageIndex (state, index) {
      state.curPageIndex = index
    },
    addFrame (state, frame) {
      state.frames.push(frame)
    },
    loadedIncrement (state) {
      state.loadedCount++
    }
  },
  actions: {
    initFrames ({ commit }) {
      const format = isSupportWebp() ? 'webp' : 'png'
      const path = format === 'webp' ? 'frames-webp' : 'frames'
      for (let i = 0; i < config.FRAME_COUNT; i++) {
        const frame = new Image()
        frame.src = require(`@/assets/${path}/${i}.${format}`)
        commit('addFrame', frame)
        frame.onload = () => {
          commit('loadedIncrement')
        }
      }
    }
  },
  modules: {
  }
})
