<template lang="pug">
  canvas.player(ref="player", width="1000", height="1000")
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'FramePlayer',
  data () {
    return {
      playerElement: null,
      context: null
    }
  },
  props: {
    frame: Number
  },
  computed: {
    ...mapState([
      'frames'
    ])
  },
  watch: {
    frame (index) {
      this.updateImage(index)
    }
  },
  methods: {
    updateImage (index) {
      this.context.clearRect(0, 0, this.playerElement.width, this.playerElement.height)
      this.context.drawImage(this.frames[index], 0, 0, 1000, 1000)
    }
  },
  mounted () {
    this.playerElement = this.$refs.player
    this.context = this.playerElement.getContext('2d')
    this.updateImage(0)
  }
}
</script>

<style scoped lang="scss">
.player {
  height: 100vh;
  width: 100vh;
  @media (max-width: 960px) {
    width: 100vw;
    height: 100vw;
  }
}
</style>
